import React, { useState, useEffect } from 'react';
import { Container } from './Slide.styles';

const useScaleLevel = (slideWidth, slideHeight) => {
  const [scale, setScale] = useState(1);

  useEffect(() => {
    const handleScaleChange = ({ currentTarget: { innerWidth, innerHeight } }) => {
      const scaleX = innerWidth / slideWidth;
      const scaleY = innerHeight / slideHeight;
      setScale(Math.min(scaleX, scaleY));
    };

    window.addEventListener('resize', handleScaleChange);
    handleScaleChange({ currentTarget: window });
    return () => {
      window.removeEventListener('resize', handleScaleChange);
    };
  });

  return scale;
};

const Slide = ({ slideWidth, slideHeight, visible, children, style }) => {
  const scale = useScaleLevel(slideWidth, slideHeight);
  console.log('slide rendered');
  return (
    <Container style={style} scale={scale} visible={visible} slideWidth={slideWidth} slideHeight={slideHeight}>
      {children}
    </Container>
  );
};

const isStillHidden = (prevProps, nextProps) => prevProps.visible === nextProps.visible;

export default React.memo(Slide, isStillHidden);
